import Swiper from 'swiper';

const calcCarousel = () => {
  const swiper = new Swiper('#cost-calc', {
    // Optional parameters
    slidesPerView: 3,
    spaceBetween: 20,
    // Navigation arrows
    slideToClickedSlide: true,
    navigation: {
      nextEl: '.calculator-change-sector__button_next',
      prevEl: '.calculator-change-sector__button_prev',
      disabledClass: 'calculator-change-sector__button_disabled'
    },
    // Responsive breakpoints
    breakpoints: {
      320: {
        slidesPerView: 1
      },
      480: {
        slidesPerView: 2
      },
      992: {
        slidesPerView: 3
      }
    }
  });

  const container = document.querySelector('#cost-calc');
  if (!container) return console.error('#cost-calc - not found');

  container.addEventListener('click', (e) => {
    const element = e.target;
    const item = element.closest('.calculator-change-sector__item-container');
    if (!item) return;

    const prevActive = container.querySelector('.calculator-change-sector__item_active');

    if (prevActive) {
      prevActive.classList.remove('calculator-change-sector__item_active');
    }

    item.classList.add('calculator-change-sector__item_active');
  });
};
export default calcCarousel;
