// Import Swiper and modules
import SwiperCore, { Navigation, Pagination, HashNavigation } from 'swiper/core';
import calcCarousel from './calc-carousel';
import plotCarousel from './plot-carousel';
import blogCarousel from './blog-carousel';
import advantageCarousel from './advantage-carousel';

const initCarousels = () => {
  // Install modules
  SwiperCore.use([Navigation, Pagination, HashNavigation]);

  // use Swiper
  calcCarousel();
  plotCarousel();
  blogCarousel();
  advantageCarousel();
};
export default initCarousels;
