import initCarousels from './modules/carousel';
import Loader from './modules/loader';
import { initMenu } from './modules/menu';
import { initTabs } from './modules/tabs/tabs';
import { initCalendar } from './modules/calendar';
import { initPlus } from './modules/plus';

import { Fancybox } from '@fancyapps/ui';

/**
 * window onload function
 */
const loader = new Loader('#loader-js', '#loader-el-js');
loader.initLoader();

const onLoadHandler = () => {
  loader.hideLoader();

  initMenu();
  initCalendar();
  initPlus();

  Fancybox.bind('#review-gallery a');

  initCarousels();

  initTabs(
    '#tabs-real-estate',
    '.real-estate-select__item',
    'real-estate-select__item_active',
    'best-real-estate__info-tab_active'
  );
  initTabs(
    '#tech-tabs',
    '.learn-more-technology__tab',
    'learn-more-technology__tab_active',
    'learn-more-technology__content_active'
  );
  initTabs(
    '#selecting-articles-tabs',
    '.selecting-articles__tab',
    'selecting-articles__tab_active',
    'selecting-articles__content_active'
  );
};

window.onload = onLoadHandler;
