import { TimelineMax } from 'gsap';

export const initTabs = (tabs, tabsNavigation, activeClsTab, activeClsContent) => {
  const tabsContainer = document.querySelector(tabs);

  if (!tabsContainer) return console.error(`${tabs} - not found`);

  const tabsButtons = tabsContainer.querySelectorAll(tabsNavigation);
  if (!tabsButtons) return console.error(`${tabsNavigation} - not found`);

  const tl = new TimelineMax();
  tabsContainer.addEventListener('click', handlerClickTab);

  function handlerClickTab(e) {
    const element = e.target;
    const $tab = element.closest(tabsNavigation);
    if (!$tab) return;

    const prevActiveTab = tabsContainer.querySelector(`.${activeClsTab}`);
    const tabId = $tab.id;
    const prevId = prevActiveTab.id;

    const currentTabContent = tabsContainer.querySelector(`._${prevId}`);
    const newTabContent = tabsContainer.querySelector(`._${tabId}`);

    if (!currentTabContent) return console.error(`._${prevId} - not found`);
    if (!newTabContent) return console.error(`._${tabId} - not found`);

    setInactiveBtn(tabsButtons);

    prevActiveTab.classList.remove(activeClsTab);
    $tab.classList.add(activeClsTab);

    tl.to(currentTabContent, {
      opacity: 0,
      duration: 0.5,
      onComplete: () => {
        currentTabContent.style.display = 'none';
        newTabContent.style.display = 'flex';

        currentTabContent.classList.remove(activeClsContent);
      }
    }).to(newTabContent, {
      opacity: 1,
      duration: 0.3,
      onStart: () => {
        newTabContent.classList.add(activeClsContent);
      }
    });
  }

  function setInactiveBtn(items) {
    items.forEach((item) => {
      item.style.pointerEvents = 'none';

      setTimeout(() => {
        if (!item.classList.contains(activeClsTab)) item.style.pointerEvents = 'initial';
      }, 1000);
    });
  }
};
