import Swiper from 'swiper';

const blogCarousel = () =>
  new Swiper('#blog-carousel', {
    // Optional parameters
    slidesPerView: 3,
    spaceBetween: 20,
    // Navigation arrows
    navigation: {
      nextEl: '.articles-list__navigation-btn_next',
      prevEl: '.articles-list__navigation-btn_prev',
      disabledClass: 'articles-list__navigation-btn_inactive'
    },
    // Responsive breakpoints
    breakpoints: {
      320: {
        slidesPerView: 1
      },
      480: {
        slidesPerView: 2
      },
      992: {
        slidesPerView: 3
      }
    }
  });

export default blogCarousel;
