import Swiper from 'swiper';

const advantageCarousel = () => {
  const swiper = new Swiper('#advantage-carousel', {
    // Optional parameters
    slidesPerView: 1,
    autoHeight: true,
    hashNavigation: {
      watchState: true
    },
    pagination: {
      el: '.advantage-description-bottom-item_pagination',
      type: 'fraction',
      formatFractionCurrent: (number) => `00${number}`.slice(-2),
      renderFraction: (currentClass) => {
        return `<span class="${currentClass}"></span>`;
      }
    },
    // Navigation arrows
    navigation: {
      nextEl: '.advantage-description-bottom-item-btn_right',
      prevEl: '.advantage-description-bottom-item-btn_left',
      disabledClass: 'advantage-description-bottom-item-btn_disabled'
    }
  });

  const navItemsContainer = document.querySelector('.advantage-fon');

  if (!navItemsContainer) return console.error('.advantage-fon - not found');

  navItemsContainer.addEventListener('click', changeActiveNavItem);
  function changeActiveNavItem(e) {
    const elem = e.target;

    const item = elem.closest('.advantage-fon-block-elem');
    if (!item) return;

    const prevActiveMenuItem = navItemsContainer.querySelector('.advantage-fon-block-elem_active');
    prevActiveMenuItem.classList.remove('advantage-fon-block-elem_active');
    item.classList.add('advantage-fon-block-elem_active');
  }
};
export default advantageCarousel;
